














































































@-webkit-keyframes croScreen {
 0%{
 	-webkit-transform: translate3d(100vw,0,0);
 	        transform: translate3d(100vw,0,0);
 }
 100%{
 	-webkit-transform: translate3d(-200vw,0,0);
 	        transform: translate3d(-200vw,0,0);
 }
}

@keyframes croScreen {
 0%{
 	-webkit-transform: translate3d(100vw,0,0);
 	        transform: translate3d(100vw,0,0);
 }
 100%{
 	-webkit-transform: translate3d(-200vw,0,0);
 	        transform: translate3d(-200vw,0,0);
 }
}

.weShow-box{
	position: relative;
	height: 10vh;
}
.weShow-msg {
	position: absolute;
	padding: 0 20px 0 15px;
	margin: 0 20px 0 0;
	border-radius: 4px;
	font-size: 0;
	background: rgba(50,50,50,.25);
	box-shadow:	inset 0 0 50px 1px rgba(250,245,255,.3),
							inset 0 0 60px rgba(50,50,50,.1),
							0 0 8px rgba(0,0,0,.3);
	cursor: pointer;
	-webkit-animation: croScreen linear forwards;
	        animation: croScreen linear forwards;
}
.weShow-msg:hover {
	box-shadow:	inset 0 0 50px rgba(250,245,255,.3),
							inset 0 0 60px rgba(50,50,50,.1),
							0 0 8px 2px rgba(0,0,0,.3);
	-webkit-animation-play-state: paused;
	        animation-play-state: paused;
	z-index: 1;
}
.msg-avatar {
	display: inline-block;
	width: 40px;
	height: 40px;
	margin: 0 10px 0 0;
	border-radius: 20px;
	vertical-align: middle;
	background: url('../assets/bg.png') center/cover;
}
.msg-ctn {
	font-size: 20px;
	line-height: 2.5;
	vertical-align: middle;
	color: rgba(255,255,255,.9);
}



